@import url(https://fonts.googleapis.com/css?family=Dosis);
body {
  margin: 0;
  padding: 0;
  font-family: 'dosis', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1 {
    font-family: 'Anton';
    font-size: 48px;
}

.App {
  text-align: center;
  width: 100%;
}

.App-section {
  -webkit-align-items: center;
          align-items: center;
  background-image: url(/static/media/star-wars-wallpaper.971535f4.jpg);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: calc(10px + 2vmin);
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  min-height: 100vh;
  opacity: 0.88;
}

.button {
    -webkit-align-self: flex-start;
            align-self: flex-start;
    padding: 12px 30px;
    text-transform: uppercase;
    display: inline-block;
    *display: inline;
    zoom: 1;
    padding: 6px 20px;
    margin: 0;
    cursor: pointer;
    border: 1px solid #bbb;
    overflow: visible;
    font: bold 13px arial, helvetica, sans-serif;
    text-decoration: none;
    white-space: nowrap;
    color: #555;

    background-color: #ddd;
    /* background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,1)), to(rgba(255,255,255,0)));
    background-image: -webkit-linear-gradient(top, rgba(255,255,255,1), rgba(255,255,255,0));
    background-image: -moz-linear-gradient(top, rgba(255,255,255,1), rgba(255,255,255,0));
    background-image: -ms-linear-gradient(top, rgba(255,255,255,1), rgba(255,255,255,0));
    background-image: -o-linear-gradient(top, rgba(255,255,255,1), rgba(255,255,255,0));
    background-image: linear-gradient(top, rgba(255,255,255,1), rgba(255,255,255,0)); */
    transition: background-color .2s ease-out;
    background-clip: padding-box; /* Fix bleeding */
    border-radius: 3px;
    box-shadow: 0 1px 0 rgba(0, 0, 0, .3), 0 2px 2px -1px rgba(0, 0, 0, .5), 0 1px 0 rgba(255, 255, 255, .3) inset;
    text-shadow: 0 1px 0 rgba(255,255,255, .9);

    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    opacity: 0.1;
}

.button:hover {
    background-color: #eee;
    color: #555;
}

.button:active {
    background: #e9e9e9;
    position: relative;
    top: 1px;
    text-shadow: none;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .3) inset;
}

.button[disabled], .button[disabled]:hover, .button[disabled]:active{
    border-color: #eaeaea;
    background: #fafafa;
    cursor: default;
    position: static;
    color: #999;
    /* Usually, !important should be avoided but here it's really needed :) */
    box-shadow: none !important;
    text-shadow: none !important;
}

.button:active {
    background-color: rgb(83, 8, 8);
    opacity: 1;
}

.-no-surprise {
    background-color: rgba(28,38,47,0.88);
}

.-surprise {
    background-color: rgba(255, 255, 255, 0.88);
}


.card-container {
    -webkit-align-items: center;
            align-items: center;
    background-color: white;
    border: 1px solid #efefef;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin:  30px auto;
    padding: 30px;
    width: 50%;
    box-shadow:
    /* The top layer shadow */
    0 1px 1px rgba(0,0,0,0.15),
    /* The second layer */
        0 10px 0 -5px #eee,
        /* The second layer shadow */
        0 10px 1px -4px rgba(0,0,0,0.15),
            /* The third layer */
            0 20px 0 -10px #eee,
            /* The third layer shadow */
        0 20px 1px -9px rgba(0,0,0,0.15);
}

.card-container > .-container {
    -webkit-align-self: center;
            align-self: center;
    margin-top: 10px;
    font-size: 0.5em;
    opacity: 1;
    width: 90%;
}

.item {
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 1px solid #cecece;
    color: #333333;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    height: 50px;
    font-family: Roboto, sans-serif;
    font-weight: 100;
    font-size: 0.6em;
    margin: 10px;
    text-align: center;
    width: 90%;
}


.MoviesList {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    max-height: 90vh;
    overflow: hidden scroll;
    padding: 0 12px 0 10px;
    position: relative;
    right: 0;
    width: 100%;
}

.MovieItem {
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-self: center;
            align-self: center;
    display: -webkit-flex;
    display: flex;
    font-size: 10px;
    list-style: none;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 9px;
    position: relative;
    min-height: auto;
    padding: 20px;
    -webkit-justify-content: center;
            justify-content: center;
    max-width: 600px;
    width: 100%;
}

.movieItem {
    -webkit-align-self: center;
            align-self: center;
    background-color: beige;
    box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.4);
    cursor: pointer;
    height: 100%;
    margin: 5px;
    position: relative;
    text-align: center;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-transform: rotatey(0deg) translatex(0px) translatey(0px);
            transform: rotatey(0deg) translatex(0px) translatey(0px);
    transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
    width: 70%;
}

.movieItem:hover {
    -webkit-transform: rotatey(20deg) translatex(0px) translatey(0px);
            transform: rotatey(20deg) translatex(0px) translatey(0px);
}

.movieItem .card-facing p {
    font-size: 14px;
    padding: 10px
}

